import React, { useEffect, useState, useRef } from 'react';
import SectionTitle from '../shared/section-titles';
import Slider from 'react-slick';
import styled from 'styled-components';
import VisibilitySensor from 'react-visibility-sensor';


import cardBg from '../../images/index/horizontal-step-bg.svg'
import stepsImage1 from '../../images/index/step-1.png'
import stepsImage2 from '../../images/index/step-2.png'
import stepsImage3 from '../../images/index/step-3.png'
import stepsImageSm from '../../images/index/steps-1-sm.svg'
import constants from '../../constants/index';
import { useStaticQuery, graphql } from 'gatsby';

import arrowRightWhite from '../../images/index/right-arrow-white.svg'
import arrowLeftWhite from '../../images/index/left-arrow-white.svg'
import Collapsible from 'react-collapsible';

const HorizontalSteps = () => {
  const data = useStaticQuery(graphql`
    query {
      bgImage: file(relativePath: {eq: "index/horizontal-steps-bg.png"}) {
        childImageSharp {
          fluid(maxHeight: 2400) {
            src
          }
        }
      }
    }
  `);
  const [currentStep, setCurrentStep] = useState(0);
  const stepImages = [stepsImage1, stepsImage2, stepsImage3];

  var sliderRef = useRef(null);
  var rowScroll = useRef(null);
  const [windowInnerWidth, setWindowInnerWidth] = useState(0);
  // TODO: remove this if not needed in future
  // const [currentSlide, setCurrentSlide] = useState(0);

  useEffect(() => {
    windowResizeListener();
    window.addEventListener('resize', windowResizeListener);

    // let rowScrollRef = rowScroll.current.addEventListener('wheel', trackScroll);

    console.log(sliderRef);
    return () => {
      // rowScrollRef.current.removeEventListener('wheel', trackScroll);
      window.removeEventListener('resize', windowResizeListener);
    };
  }, []);

  const windowResizeListener = () => {
    setWindowInnerWidth(window.innerWidth);
  }

  const beforeChangeSlider = async (current, next) => {
    console.log(`{current: ${current}, next: ${next}}`)
    setCurrentStep(next);
  }

  return (
    <>
      <section id='horizontal-steps'>
        <SectionWrapper
          bg={data.bgImage.childImageSharp.fluid.src}
        >
          <div className='d-flex justify-content-center align-items-center'>
            <div>
              <SectionTitle className='text-center'>
                <h2 className='text-white'>Научи стъпките и за нула време<br />ще танцуваме заедно</h2>
                <hr className='center white' />
              </SectionTitle>
              <div className='row w-100 mt-0'>
                <div className='container text-center'>
                  <div className='d-none d-xl-block'>
                    <img src={stepImages[currentStep]} className='img-fluid img-negative-top order-0 order-md-1' alt='Стъпки за да се присъединиш към NetSurf' />
                  </div>
                  <div className='d-block d-xl-none d-flex justify-content-center w-100 mb-5'>
                    <img src={stepsImageSm} className='img-fluid' alt='Стъпки за да се присъединиш към NetSurf' />
                  </div>

                  <div className='row d-none d-xl-block my-xl-5 order-1 order-md-0'>
                    <VisibilitySensor>
                      <div className='col-12' ref={rowScroll}>
                        <Slider
                          {...settings}
                          id='horizontal-slides'
                          ref={sliderRef}
                          nextArrow={<NextArrow />}
                          prevArrow={<PrevArrow />}
                          beforeChange={beforeChangeSlider}
                        >
                          {constants.horizontalCards.map(({ title, icon, text, image }, i) => (
                            <div key={i} className='d-flex justify-content-center'>
                              <Card
                                title={title}
                                icon={icon}
                                text={text}
                                image={image}
                                windowWidth={windowInnerWidth}
                                cardIndex={i}
                              />
                            </div>
                          ))}
                        </Slider>
                        {/* <h6 className='d-none d-xl-block text-white'>Скролни за следващата стъпка</h6> */}
                      </div>
                    </VisibilitySensor>
                  </div>

                  <div className='row d-block d-xl-none order-1 order-md-0'>
                    <div className='col-12' ref={rowScroll}>
                      {constants.horizontalCards.map(({ title, icon, text, image }, i) => (
                        <div key={i} className='d-flex justify-content-center'>
                          <Card
                            title={title}
                            icon={icon}
                            text={text}
                            image={image}
                            windowWidth={windowInnerWidth}
                            cardIndex={i}
                          />
                        </div>
                      ))}
                      <h6 className='d-none d-xl-block text-white'>Скролни за следващата стъпка</h6>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </SectionWrapper>
      </section>
    </>
  )
}

const Card = ({ windowWidth, text, image, title, icon, cardIndex }) => {

  const CardHeaderSm = ({ title, icon, isOpen }) => (
    <>
      <div className='p-3 text-left row'>
        <div className='col-12 col-sm-3 d-flex justify-content-center align-items-center'>
          <img src={icon} className='img-fluid' alt='Икона на интерактивна картичка' />
        </div>
        <div className='col-10 col-sm-7 d-flex justify-content-start align-items-center'>
          {title}
        </div>
        <div className='col-2 col-sm-1 d-flex justify-content-center align-items-center flex-column'>
          <span className={'icon ' + (isOpen ? 'arrow-close' : 'arrow-open')}></span>
        </div>
      </div>
    </>
  )

  if (windowWidth < 1200) {
    return (
      <>
        <div className='col-12 p-0 m-0 my-2 '>
          <CardWrapperSm>
            <Collapsible
              open={cardIndex === 0}
              trigger={<CardHeaderSm title={title} icon={icon} isOpen={true} />}
              triggerWhenOpen={<CardHeaderSm title={title} icon={icon} isOpen={false} />}
            >
              <div className='col-12 mt-1 d-flex justify-content-center align-items-center'>
                {text}
              </div>
            </Collapsible>

          </CardWrapperSm>
        </div>
      </>
    )
  } else {
    return (
      <>
        <div className='row d-flex justify-content-end'>
          <div className='col-6'>
            <CardWrapper className='horizontal-steps-card'>
              <CardContainer className='text-left'>
                <div className='row'>
                  <div className='col-4'>
                    <img src={icon} className='img-fluid' alt='Икона на интерактивна картичка' />
                  </div>
                  <div className='col-8 d-flex justify-content-start align-items-center'>
                    {title}
                  </div>
                </div>
                <div className='col-12 mt-2 px-3'>
                  {text}
                </div>
              </CardContainer>
            </CardWrapper>
          </div>
          <div className='col-xl-3 d-none d-xl-block'>
            <img src={image} className={'img-fluids card-image-' + cardIndex} alt='Снимка на интерактивна картичка' />
          </div>
        </div>
      </>
    )
  }
}

const CardWrapperSm = styled.div`
  background: #FFFFFF;
  box-shadow: 0px 0px 102px rgba(17, 41, 70, 0.25);
  border-radius: 16px;
`;

const CardWrapper = styled.div`
  /* background-image: url(${cardBg});
  background-repeat: no-repeat;
  background-position: top; */
  /* background-size: 200% 105%; */
  background: #fff;
  border-radius: 8px;
  /* width: auto; */
  font-size: 16px;
  padding: 1rem 0.5rem 1rem 0.5rem;
  box-shadow: 0px 0px 102px rgba(17, 41, 70, 0.25);
`;

const CardContainer = styled.div`
  /* padding-top: 110px; */
`;

const SectionWrapper = styled.div`
  background-image: url(${props => props.bg});
  /* background-size: cover; */
  /* min-height: 1650px; */
  /* padding-top: 15rem; */

  background-size: 100% 90%;
  /* background-size: cover; */
  background-position: center;
  background-repeat: no-repeat; 
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: 1200px) {
    min-height: 1800px;
  }

  @media screen and (min-width: 1200px) {
    min-height: 1500px;
  }

  @media screen and (min-width: 1800px) {
    min-height: 1500px;
  }

  @media screen and (max-width: 991px){
    background-size: cover;
    min-height: 1200px;

  }

  @media screen and (max-width: 576px) {
    min-height: 1000px;
    padding-top: 11rem;
  }


`;

const NextArrow = (props) => (
  <img
    {...props}
    src={arrowRightWhite}
    alt="Arrow Right"
    className={props.className + ' img-fluid'}
    style={{ ...props.style, display: 'block' }}
    onClick={props.onClick}
  />
);

const PrevArrow = (props) => (
  <img
    {...props}
    src={arrowLeftWhite}
    alt="Arrow Left"
    className={props.className + ' img-fluid'}
    style={{ ...props.style, display: 'block' }}
    onClick={props.onClick}
  />
);


const settings = {
  arrows: true,
  speed: 1000,
  infinite: false,
  centerMode: false,
  autoplay: false,
  autoplaySpeed: 5000,
  slidesToShow: 1,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  // beforeChange: beforeChangeSlider,
  responsive: [
    {
      breakpoint: 700,
      settings: {
        arrows: false,
        dots: true,
      }
    },
  ]
}


export default HorizontalSteps;